$font:'Helvetica Neue',sans-serif;

html,body {
    width: 100% !important; 
    height: 100% !important;
    position: absolute; 
    background: #F6F6F6;

}

.contact-body {
    padding-top: 5em;
    padding-bottom: 3em;
    
    @media(max-width:1024px){
        display:grid;   
        grid-template-columns: 1fr; 
    }
    

    h1 {
        text-align:center;
        font-family:$font;
        font-size:3vw;
        color:#434343;
        padding-bottom: 2rem;
        @media(max-width:1024px){
            font-size:8vw;
            padding-bottom: 1rem;
        }
    }
    h2 {
        text-align:center;
        font-family:$font;
        color:#6C6C6C;
        font-size:2vw;
        padding-bottom:2em;
        @media(max-width:1024px){
            font-size:4vw;
        }
    }

    .contact-grid {
        display:grid;
        grid-template-columns: repeat(2,1fr);
        @media(max-width:1024px){
            display:grid;   
            grid-template-columns: 1fr;    
        }
        div{
            img{
                width:60%;
                display:block;
                margin:0 auto;
                
            }
        }

      
    }
}

