div{
    h2.AboutText-heading{
        font-weight:bold;
        color:#434343; 
        font-family:"Helvetica Neue", sans-serif;
        font-size: 2vw;
        padding:1rem;
        @media(max-width:1024px){
            font-size: 5vw;
        }
        @media(max-width:450px){
            font-size: 7vw;
        }
    }
    p.AboutText-text{
        color:#6C6C6C;
        font-size:2rem !important;
        line-height:3rem;
        font-family:"Helvetica Neue", sans-serif;
        @media(max-width:450px){
            font-size: 1rem !important;
            line-height:1rem;
        }
    }
}

