$font:'Helvetica Neue',sans-serif;

.Projects-grid {
    background:#F6F6F6;
    width: 100% !important; 
    height: 100% !important;
    position: absolute; 
    padding-top:5rem;
    display:grid;
    grid-template-columns: 1fr;
    .Projects-Content {
        padding:3rem;
        display:grid;
        grid-template-columns: repeat(3,1fr);
        grid-column-gap: 5rem;
        @media(max-width: 1024px){
            grid-template-columns: 1fr;
            width: 100%;
        }

    }
}