.page-content {
  background: #F6F6F6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #F6F6F6, #F6F6F6);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F6F6F6, #F6F6F6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
