.aboutme{
    background:#F6F6F6;
    text-align: center;
    position: relative;
    top:0;
    left:0;
    width:100%;
    height:100%;
    @media(max-width:450px) {
        padding-top:3rem;
    }
    p{
        font-family:"Avenir Next";
        display:inline;
        font-size:20vw;
        z-index:2;
        position: relative;
        @media(max-width:1024px){
            font-size: 30vw;
        }
        @media(max-width:450px){
            font-size: 30vw;
        }
    }
    .aboutme-heading{
        font-weight:bold;
        color:#F6F6F6; 
        text-shadow:2px 2px 17px rgba(0,0,0,0.5);
        font-size:8vw;
        display:inline;
        font-family:"Helvetica Neue", sans-serif;
        z-index:1;
        position:relative;
        @media(max-width:1024px){
            font-size:16vw;
            overflow-wrap:break-word;
        }
        @media(max-width:450px){
            font-size:16vw;
            overflow-wrap:break-word;
        }
    }
    .aboutme-flex1 {
        z-index:8;
        position: relative;
        display:grid;
        grid-template-columns: repeat(2,1fr);
        padding-bottom:3rem;
        @media(max-width:1024px){
            grid-template-columns: 1fr;
        }
        @media(max-width:450px){
            grid-template-columns: 1fr;
        }

    }
    .aboutme-flex2 {
        z-index:9;
        position: relative;
        display:grid;
        grid-template-columns: repeat(2,1fr);
        @media(max-width:1024px){
            grid-template-columns: 1fr;
        }
        @media(max-width:450px){
            grid-template-columns: 1fr;
        }
        div{
            h2{
                font-weight:bold;
                color:#434343;
                font-family:"Helvetica Neue", sans-serif;
                font-size:2vw;
                padding:1rem;
                @media(max-width:1024px){
                    font-size:5vw;
                }
                @media(max-width:450px){
                    font-size:7vw;
                }
            }
        }
        
    }

}
img.aboutme-img{
    top:25%;
    left:48%;
    position:fixed;
    z-index:0;
}