$font:"Helvetica Neue",sans-serif;

.LandingPage{
    width: 100% !important; 
    height: 100% !important;
    position: absolute; 
    padding-top:5rem;
    background:#F6F6F6;
        div {
            img{
                width: 20%;
                @media(max-width:1024px){
                    width: 55%;
                }
                @media(max-width:450px){
                    width: 55%;
                }
            }
        }
        div{
            text-align:center;

            h2 {
                font-weight:bold;
                font-family:$font;
                color:#6C6C6C;
                font-size:3vw;
                @media(max-width:1024px){
                    font-size:10vw;
                    padding-top:4rem;
                }
                @media(max-width:450px){
                    font-size:10vw;
                    padding-top:4rem;
                }
                @media(max-width:350px){
                    font-size:10vw;
                    padding-top:3rem;
                }
            }
            h1 {
                font-weight:bold;
                font-family:$font;
                color: #F6F6F6;
                text-shadow:2px 2px 17px rgba(0,0,0,0.5);
                font-size: 10vw;
                @media(max-width:1024px){
                    font-size:16vw;
                }
                @media(max-width:450px){
                    font-size:16vw;
                    padding-bottom: 4rem;
                }
                @media(max-width:350px){
                    font-size:16vw;
                    padding-bottom: 2rem;
                }

                
            }
            div {
                p {
                    font-size:1vw !important;
                    color:#434343;
                    font-weight:bold;
                    @media(max-width:1025px){
                        font-size:1vw !important;
                    }
                    @media(max-width:450px){
                        font-size:1vw !important;
                    }
                }
            }

        }

}

// Button only

.LandingPage-btn-text {
    color: black !important;
}

.LandingPage-btn-text:active {
    color: white !important;

}

.fixed-misc {
    position: fixed;
    top:30%;
    right:-160px;
    transform: rotate(-90deg);
    font-weight:700;
    text-transform: uppercase;
    font-size:.875rem;
    letter-spacing:1.5px;
    color:white;
    mix-blend-mode: difference;
    @media (max-width: 450px) {
        right: -100px;
        font-size:0.5rem;
    }
}